input,
textarea,
select,
button,
a {
	font-family: 'roboto', Arial, sans-serif; }

h1 {
	font-size: $fs-xxxl;
	font-family: 'plumb';
	font-weight: 400;
	letter-spacing: .07em; }
h2 {
	font-size: $fs-l;
	font-weight: 400; }
h3 {
	font-size: $fs-xm;
	font-family: 'plumb';
	font-weight: 700;
	letter-spacing: 3.6px;
	line-height: $lh-sm; }
h4 {
	font-size: $fs-m;
	font-family: 'plumb';
	font-weight: 400; }
h5 {
	// font-size: $fs-xxxl
	font-family: 'plumb';
	font-weight: 400; }
h6 {
	// font-size: $fs-xxxl
	font-family: 'plumb';
	font-weight: 400; }

p {
	font-size: $fs-m; }

.text-blue {
	color: $blue-500; }

.text-red {
	color: $red-500; }

.clipboard_copy {
	cursor: pointer;
	display: inline-block; }

.text-gradient {
	display: inline-block;
	color: transparent;
	background: linear-gradient(to left, $green-400, $blue-400);
	-webkit-background-clip: text;
	background-clip: text;
	@include ie-screen {
		color: $blue-400;
		background: none; } }

.ui-tooltip {
	color: white;
	position: absolute;
	z-index: 9990;
	padding: 5px 5px;
	font-size: 11px;
	line-height: 16px;
	text-align: center;
	border-radius: 3px;
	letter-spacing: 0.025em;
	max-width: calc((1680px - 10vw) / 3 - 32px + 96px);
	background-color: $black; }

.ui-helper-hidden-accessible {
	display: none; }
