.hero {
	overflow: hidden;
	height: 100vh;
	max-height: 850px;
	background-size: cover;
	background-repeat: no-repeat;
	background-color: $blue-600;
	background-position: 50% 50%;
	&.is-main-top {
		padding: 90px 0 180px; }
	&.is-main-midle {
		height: 744px;
		padding: 94px 0 330px; }
	color: $white;
	.container,
	&-content {
		height: 100%;
		vertical-align: middle; }
	&-header {
		text-transform: uppercase;
		line-height: 38px;
		margin-bottom: 20px; }
	&-title {
		font-size: $fs-xxxm;
		line-height: 1.6;
		opacity: .8;
		font-weight: 200;
		margin-bottom: 30px; }
	&-text-wrap {
		@extend %extreme-left-col;
		border-left: 2px solid $green-300;
		padding-left: ($base-padding * 2) - 3; } }
