.clients_slider {
	padding: 36px 0 22px;
	min-height: 130px;
	overflow: hidden;
	&-row {
		float: left;
		width: calc( 100% + #{$base-padding} * 4 );
		min-height: 107px;
		align-items: center; }
	&-header {
		width: 0;
		transform: scale(-1);
		writing-mode:  tb-rl;
		color: $grey-500;
		font-size: $fs-m;
		white-space: nowrap;
		float: left;
		margin-left: -6px; }
	&-item {
		text-align: center;
		img {
			display: inline-block;
			max-width: 100%;
			vertical-align: middle;
			width: auto; } }
	&-logo1 {
		max-height: 32px; }
	&-logo2 {
		max-height: 46px; }
	&-logo3 {
		max-height: 83px; }
	&-logo4 {
		max-height: 90px; } }
